@import "../../../scss/mixin.scss";

.EmptyCartBlock {
	@include widthHeight(100%, 500px);
	@include flex(column, center, space-evenly);
	.smile {
		font-size: 55px;
	}
	.description {
		@include flex(column, center, none);

		& span {
			text-align: center;
		}
	}

	.backBtn {
		@include widthHeight(170px, 40px);
		@include flex(row, center, space-evenly);
		font-size: 15px;
		line-height: 2px;
		border-radius: 27px;
		border: 1px solid gray;
		background-color: rgba(0, 0, 0, 0);
		color: gray;
		text-decoration: none;
		cursor: pointer;
		box-sizing: border-box;

		&:active {
			transition: 0.7s linear;
			transform: scale(0.9);
		}
	}
}
