@import "../../scss/var";
@import "../../scss/mixin";

.cartBlock {
	margin-top: 50px;
	margin-bottom: 50px;
	@include widthHeight(100%, 100%);
	@include flex(column, center, center);

	.cartBlockWrap {
		@include widthHeight(70%, 100%);

		@media (max-width: 1020px) {
			width: calc(100vw - 200px);
		}

		@media (max-width: 841px) {
			width: calc(100vw - 160px);
		}

		@media (max-width: 557px) {
			width: calc(100vw - 100px);
		}

		@media (max-width: 420px) {
			width: calc(100vw - 75px);
		}
		.titleAndBtnWrap {
			@include widthHeight(100%, 50px);
			@include flex(row, center, space-between);
			margin-bottom: 20px;

			.title {
				width: 120px;
				font-size: 22px;
				@include flex(row, center, space-evenly);
			}

			.cleanCartBtn {
				@include widthHeight(170px, 40px);
				@include flex(row, center, space-evenly);
				font-size: 18px;
				letter-spacing: 1px;
				border-radius: 27px;
				border: 1px solid gray;
				color: gray;
				background-color: rgba(0, 0, 0, 0);
				cursor: pointer;
				overflow: visible;

				&:active {
					transform: scale(0.9);
					transition: 0.1s ease-out;
				}

				.cleanCartBtnTitle {
					font-size: 14px;
				}

				@media (max-width: 600px) {
					width: 60px;
					.cleanCartBtnTitle {
						display: none;
					}
				}
			}
		}

		.sumItemsAndMoneyWrap {
			@include widthHeight(100%, 50px);
			@include flex(row, center, space-between);
		}

		.btnWrap {
			@include widthHeight(100%, 50px);
			@include flex(row, center, space-between);

			.backBtn {
				@include widthHeight(170px, 40px);
				@include flex(row, center, space-evenly);
				font-size: 15px;
				line-height: 3px;
				border-radius: 27px;
				border: 1px solid gray;
				background-color: rgba(0, 0, 0, 0);
				color: gray;
				text-decoration: none;
				cursor: pointer;
				box-sizing: border-box;

				&:active {
					transition: 0.7s linear;
					transform: scale(0.9);
				}

				@media (max-width: 600px) {
					width: 60px;
					.backBtnTitle {
						display: none;
					}
				}
			}

			.buyBtn {
				@include widthHeight(170px, 40px);
				background-color: darkorange;
				color: white;
				border: none;
				border-radius: 27px;
				cursor: pointer;

				@media (max-width: 600px) {
					width: 100px;
				}
			}
		}
	}
}
