@import "../../../../scss/mixin";

.button {
	width: 140px;
	height: 35px;
	@include flex(row, center, space-evenly);
	border: 1px solid darkorange;
	border-radius: 20px;
	background-color: white;
	color: darkorange;
	cursor: pointer;

	&:active {
		transform: scale(0.9);
		transition: 0.5s linear;
	}
}
