@import "../../../scss/mixin";
@import "../../../scss/var";

.cartItemWrap {
	@include widthHeight(100%, 100px);
	box-shadow: 0 0 4px 0.5px rgba(34, 60, 80, 0.4);
	border-radius: 27px;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-rows: 1fr;
	grid-column-gap: 50px;
	place-items: center;
	margin-bottom: 35px;
	padding-left: 15px;

	@media (max-width: 787px) {
		grid-column-gap: 20px;
	}

	@media (max-width: 722px) {
		height: 130px;
		padding-top: 20px;
		grid-template-columns: repeat(3, 1fr);
		grid-template-rows: repeat(2, 1fr);
		gap: 0px 0px;
		grid-template-areas:
			"pizza sum delete"
			"count count count";

		.descriptionAndImgWrap {
			grid-area: pizza;
		}
		.incAndDecBtnWrap {
			grid-area: count;
		}
		.sumMoney {
			grid-area: sum;
		}
		.deleteItemBtn {
			grid-area: delete;
		}
	}

	@media (max-width: 511px) {
		grid-template-columns: repeat(2, 1fr);
		grid-template-rows: repeat(2, 1fr);

		grid-template-areas:
			"pizza sum "
			"count delete";
	}

	@media (max-width: 426px) {
		position: relative;
		padding-top: 15px;
		padding-left: 0;
		height: 250px;
		grid-template-columns: repeat(2, 1fr);
		grid-template-rows: repeat(3, 1fr);
		gap: 0 0;
		grid-template-areas:
			"pizza pizza"
			"pizza  pizza"
			"count  sum";
		place-items: center;
	}

	.descriptionAndImgWrap {
		width: 250px;
		@include flex(row, center, flex-start);

		.imgWrap {
			@include widthHeight(80px, 80px);

			@media (max-width: 426px) {
				@include widthHeight(120px, 120px);
				padding-top: 5px;
				transform: translateX(7px);
			}

			& img {
				@include widthHeight(100%, 100%);
			}
		}

		.descriptionWrap {
			padding-left: 15px;

			@include flex(column, flex-start, flex-start);

			@media (max-width: 426px) {
				@include flex(column, center, center);
				padding-left: 0;
				width: 100%;
			}

			.title {
				font-weight: bold;
			}

			.description {
				font-size: 14px;
				color: gray;
			}
		}

		@media (max-width: 426px) {
			flex-direction: column;
		}
	}

	.incAndDecBtnWrap {
		width: 120px;
		@include flex(row, stretch, space-evenly);

		.incDecBtn {
			background-color: rgba(0, 0, 0, 0);
			border: none;
			color: darkorange;
			font-size: 24px;
			cursor: pointer;
			transform: translateY(3px);

			@media (max-width: 420px) {
				font-size: 25px;
				padding: 0;
			}
		}

		.num {
			font-weight: bold;

			@media (max-width: 420px) {
				font-size: 19px;
			}
		}
	}

	.line {
		display: none;

		@media (max-width: 426px) {
			background-color: rgba(34, 60, 80, 0.4);
			display: block;
			position: absolute;
			bottom: 70px;
			@include widthHeight(82%, 1px);
		}
	}

	.sumMoney {
		font-weight: bold;

		@media (max-width: 426px) {
			font-size: 19px;
			transform: translateY(-4px);
		}
	}

	.deleteItemBtn {
		background-color: rgba(0, 0, 0, 0);
		border: none;
		color: gray;
		font-size: 24px;
		cursor: pointer;
		transform: translateY(3px);

		@media (max-width: 426px) {
			position: absolute;
			top: 15px;
			right: 20px;
			font-size: 29px;
			padding: 0;
		}
	}
}
